import {
    user as UserApi
} from 'config/request.js';

import {
    ListData
} from 'common/';


module.exports = {
    name: 'list',
    components: {
        ListData
    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'username',
                label: ''
            }, {
                key: 'email',
                label: ''
            }, {
                key: 'create_time',
                label: '',
                sort: true
            }, {
                key: 'sex',
                label: '',
                formatter: function(item) {
                    return item.sex == 1 ? '' : (item.sex == 2 ? '' : '');
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }, {
                    text: '',
                    value: 3
                }],
                filter_method: function(value, item) {
                    return item.sex == value;
                },
                filter_multiple: true,
            }, {
                key: 'status',
                label: '',
                formatter: function(item) {
                    return item.status == 1 ? '' : '';
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }],
        }
    },
    methods: {
        /**
         * 
         */
        getList() {
            var data = {};
            UserApi.selectUser.call(this, data, (data) => {
                this.list = data.list;
            });
        },

        onChangeCheckbox(ids, datas) {
            // console.log(ids);
            // console.log(datas);
        },

        onDelete(opts) {
            // this.$refs.list.onUpdateList(opts.batch_ids || opts.index);
        }
    },
    mounted() {
        this.getList();
    },
    watch: {
        '$route' (to, from) {

        }
    }
}


